.admin-container {
  display: flex;
  min-height: 100vh;
  background: #121212;
}

/* ì‚¬ì´ë“œë°” ìŠ¤íƒ€ì¼ */
.sidebar {
  width: 220px;
  min-width: auto; /* âœ… í¬ê¸° ê°•ì œ ê³ ì • í•´ì œ */
  background: #2c2c2c;
  padding: 10px;
}

.sidebar h2 {
  font-size: 20px;
  margin-bottom: 15px;
}

/* ì‚¬ì´ë“œë°” ë©”ë‰´ ìŠ¤íƒ€ì¼ */
.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar li {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  cursor: pointer;
  background: #3a3a3a;
  color: #dcdcdc;
  transition: 0.3s;
}

.sidebar li:hover,
.sidebar .active {
  background: #505050;
  color: #fff;
}

.sidebar .active {
  background: #ff4757;
  font-weight: bold;
}

/* ë©”ì¸ ì½˜í…ì¸  */
.content {
  flex: 1;
  padding: 30px;
  overflow-x: auto; /* âœ… ê°€ë¡œ ìŠ¤í¬ë¡¤ ë¬¸ì œ í•´ê²° */
}

.cta-button {
  background: light;
  color: dark;
  border: none;
  padding: 1rem 2.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  z-index: 1;
  margin: 0 20px;
  vertical-align: middle;
}

.cta-button:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.mypage-btn {
  height: 60px;
  width: 60px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  z-index: 1;
  margin: 0 20px;
  vertical-align: middle;
}

.mypage-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}
